.cont-section{
    display: block;
    height:auto;
}

.cont-wrapper {
    position:relative;
    display: flex;
    width: 100%;
    height:100%;
    overflow:hidden;
    padding-top: 65px;
}

.cont-wrapper video {
    display: flex;
    position: absolute;
    top: 65;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.cont-wrapper .cont-text{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    margin:50px;
    background-color: #fff;
    text-align: center;
    mix-blend-mode: screen;
    text-transform: uppercase;
    font-size: 100px;
    font-weight: 800;
}

@media screen and (max-width:960px){
    .cont-wrapper .cont-text{
        display: flex;
        justify-content: center;
        width: 100%;
        height: 100%;
        margin:50px;
        background-color: #fff;
        mix-blend-mode: screen;
        text-transform: uppercase;
        font-size: 50px;
        font-weight: 800;
    }
}

.cont-wrapper-2 {
    position:relative;
    display: flex;
    width: 100%;
    height:100%;
    overflow:hidden;
}

.cont-inner-wrapper{
    position:relative;
    display: flex;
    background-color: #00cdcd;
    width: 100%;
    height:100%;
    overflow:hidden;
    margin-left: 40px;
    margin-right: 40px;
}

.cont-inner-wrapper-2{
    position:relative;
    display: flex;
    /* background-color: #00cdcd; */
    width: 100%;
    height:100%;
    overflow:hidden;
    margin-left: 40px;
    margin-right: 40px;
}

.cont-inner-text {
    display: block;
    padding: 30px;
    width: 100%;
    height: 100%;
    margin: 15px;
}


.cont-inner-text2{
    display: flex;
	flex-wrap: wrap;
	justify-content: center;
    margin-left: auto;
    margin-right: auto;
}

.cont-top-text{
    font-size: 70px;
    font-family: 'Poppins',cursive;
}

.cont-down-text{
    padding-top: 15px;
    font-size: 25px;
    text-align: justify;
    margin-bottom: 40px;
    font-family: 'Poppins',cursive;
}

@media screen and (max-width:960px){
    .cont-top-text{
        font-size: 44px;
    }
    
    .cont-down-text{
        padding-top: 10px;
        font-size: 20px;
        text-align: justify;
    }
}
@media screen and (max-width:760px){
    .cont-top-text{
        font-size: 34px;
    }
    
    .cont-down-text{
        padding-top: 5px;
        font-size: 15px;
        text-align: justify;
    }
}

.cont-container{
    width:100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 30px;
}

.cont-container .cont-info {
    width: 50%;
    display: flex;
    flex-direction: column;
}

.cont-container .cont-info .cont-box {
    position: relative;
    padding:20px 0;
    margin: 20px;
    display: flex;
}
.cont-box{
    background-color:#fff;
}

.cont-container .cont-info .cont-box .cont-icon {
    min-width: 60px;
    height: 60px;
    background-color:#00cdcd ;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    font-size: 22px;
}

.cont-container .cont-info .cont-box .cont-text{
    display: flex;
    margin-left: 20px;
    font-size: 16px;
    color: #000;
    flex-direction: column;
    font-weight: 300;
}

.cont-container .cont-info .cont-box .cont-text h3{
    font-weight: 700;
    color:#00bcd4;
}

.cont-text p{
    font-weight: 500;
}

.cont-form{
    width:90%;
    padding: 40px;
    background-color: #fff;
    display: flex;
    justify-content: center;
    font-family: 'Poppins',cursive;
}

.cont-form h2{
    font-size: 70px;
    color:#333;
    font-weight: 500;
    text-align: center;
    margin-bottom: 50px;
    font-family: 'Poppins',cursive;
}

.cont-form .inputBox{
    position: relative;
    width: 100%;
    margin-top: 10px;
    display: inline;
    
    
}

.cont-form .inputBox1{
    position: relative;
    width: 100%;
    margin-top: 20px;
    display: inline;
    padding-left: 15px;
    padding-right: 15px;
     
}

.cont-form .inputBox input,
.cont-form .inputBox textarea,
.cont-form .inputBox select
{
    width: 50%;
    padding: 5px 0;
    font-size: 16px;
    margin:10px 0px;
    border: none;
    border-bottom: 2px solid #333;
    outline: none;
    resize: none;
    background-color: #fff;
}

.cont-form .inputBox1 input,
.cont-form .inputBox1 select
{
    width: 45%;
    padding: 5px 0;
    font-size: 16px;
    margin:50px 0px;
    border: none;
    border-bottom: 2px solid #333;
    outline: none;
    resize: none;
    background-color: #fff;
}

.cont-form .inputBox1 textarea{
    width: 60%;
    padding: 5px 0px;
    font-size: 16px;
    margin:50px 0px;
    border: none;
    border-bottom: 2px solid #333;
    outline: none;
    resize: none;
    background-color: #fff;
}

.cont-form .inputBox span{
    position: absolute;
    left:0;
    padding: 5px 0;
    font-size: 18px;
    margin: 10px 0;
    pointer-events: none;
    transition:0.5s;
    color: #666;
    font-family: 'Poppins',cursive;
}

.cont-form .inputBox1 span{
    position: absolute;
    left:0;
    padding: 5px 15px;
    font-size: 20px;
    margin: 20px 0;
    pointer-events: none;
    transition:0.5s;
    color: #666;
}

.cont-form .inputBox1 select option{
    font-family: 'Poppins',cursive;
    font-size: 20px;
}


.cont-form .inputBox input:focus ~ span,
.cont-form .inputBox input:valid ~ span,
.cont-form .inputBox textarea:focus ~ span,
.cont-form .inputBox textarea:valid ~ span,
.cont-form .inputBox select:focus ~ span,
.cont-form .inputBox select:valid ~ span{
    color: #e91e63;
    font-size: 14px;
    transform: translateY(-20px);
}

.cont-form .inputBox1 input:focus ~ span,
.cont-form .inputBox1 input:valid ~ span,
.cont-form .inputBox1 textarea:focus ~ span,
.cont-form .inputBox1 textarea:valid ~ span
{
    color: #360c65;
    font-size: 16px;
    transform: translateY(-25px);
}

.cont-form .inputBox input[type="submit"]{
    width:100%;
    background: #00bcd4;
    color: #fff;
    border: none;
    cursor: pointer;
    padding: 10px;
    font-size: 18px;
}

.cont-form .inputBox1 .contact-button{
    width:30%;
    background: #00bcd4;
    margin-left: 400px;
    margin-right:400px;
    color: #fff;
    border: none;
    cursor: pointer;
    padding: 10px;
    font-size: 20px;
    font-family: 'Poppins',cursive;
}

@media screen and (max-width: 991px){
    .cont-container{
        flex-direction: column;
    }

    .cont-container .cont-info{
        margin-bottom: 40px;
    }

    .cont-container .cont-info,
    .cont-container .cont-form{
        width: 100%;
    }

    .cont-form .inputBox1 input,
.cont-form .inputBox1 select
{
    width: 100%;
    padding: 5px 0;
    font-size: 16px;
    margin:30px 0px;
    border: none;
    border-bottom: 2px solid #333;
    outline: none;
    resize: none;
    background-color: #fff;
}

.cont-form .inputBox1 textarea{
    width: 100%;
    padding: 5px 0px;
    font-size: 16px;
    margin:30px 0px;
    border: none;
    border-bottom: 2px solid #333;
    outline: none;
    resize: none;
    background-color: #fff;
}

.cont-form .inputBox1 .contact-button{
    width:100%;
    background: #00bcd4;
    color: #fff;
    border: none;
    cursor: pointer;
    padding: 10px;
    font-size: 18px;
}
}