.bigtext {
    color: white;
    text-transform: capitalize;
    letter-spacing: .5px;
    font-size: 70px;
    font-weight: 700;
    margin: 0 0 15px;
    margin-bottom: 20px;
    display: block;
    font-family: 'Poppins',cursive;
}

@media screen and (max-width:520px) {
    .bigtext{font-size:50px;}
    
}

.WorkRow2 {
    display: flex;
	align-items: center;
	justify-content: center;
	flex-wrap: wrap;
    margin-top: 0px;
    width:100%;
}

.WorkBox1 {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  /* width: calc(720px - 30px);
  height: calc(400px - 30px); */
  width: calc(630px - 30px);
  height: calc(400px - 30px);
  background: #000;
  float: left;
  margin: 15px;
  margin-right: 30px;
  box-sizing: border-box;
  overflow: hidden;
  border-radius: 10px;
  margin-bottom:60px;
  
}

@media screen and (max-width:680px){
    .WorkBox1{
        
        width: calc(300px - 30px);
        height: calc(400px - 30px);
        
    }
    .imgw {
        width:100%;
        height:100%;
        object-fit: contain;
        transition: all 1s;
    }
}

.WorkBox2 {
    display: flex;
    justify-content: center;
    width: calc(300px - 30px);
    height: calc(400px - 30px);
    background: #000;
    float: left;
    margin: 15px;
    box-sizing: border-box;
    overflow: hidden;
    border-radius: 10px;
    margin-bottom:60px;
    
  }

  .imgw {
      width:100%;
      height:100%;
      object-fit: cover;
      transition: all 1s;
  }

  .imgw:hover{
      transform: scale(1.1);
  }

