.about-section{
    display: block;
    height:auto;
}

.about-wrapper {
    position:relative;
    display: flex;
    width: 100%;
    height:100%;
    overflow:hidden;
    padding-top: 65px;

}

.about-wrapper video {
    display: flex;
    position: absolute;
    top: 65;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.about-wrapper .about-text{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    margin:50px;
    background-color: #fff;
    text-align: center;
    mix-blend-mode: screen;
    text-transform: uppercase;
    font-size: 100px;
    font-weight: 800;
}

@media screen and (max-width:960px){
    .about-wrapper .about-text{
        display: flex;
        justify-content: center;
        width: 100%;
        height: 100%;
        margin:50px;
        background-color: #fff;
        mix-blend-mode: screen;
        text-transform: uppercase;
        font-size: 50px;
        font-weight: 800;
    }
}

.about-wrapper-2 {
    position:relative;
    display: flex;
    width: 100%;
    height:100%;
    overflow:hidden;
}

.about-inner-wrapper{
    position:relative;
    display: flex;
    background-color: #00cdcd;
    width: 100%;
    height:100%;
    overflow:hidden;
    margin-left: 40px;
    margin-right: 40px;
}


.about-inner-text {
    display: block;
    padding: 30px;
    width: 100%;
    height: 100%;
    margin: 15px;
}


.about-top-text{
    font-size: 70px;
    font-family: 'Poppins',cursive;
}

.about-top-text-2{
    text-align: center;
    font-size: 54px;
}

.about-down-text{
    padding-top: 15px;
    font-size: 25px;
    font-family: 'Poppins',cursive;
    text-align: justify;
    margin-bottom: 40px;
}

@media screen and (max-width:960px){
    .about-top-text{
        font-size: 44px;
    }

    .about-top-text-2{
        text-align: center;
        font-size: 44px;
    }
    
    .about-down-text{
        padding-top: 10px;
        font-size: 20px;
        text-align: justify;
    }
}
@media screen and (max-width:760px){
    .about-top-text{
        font-size: 34px;
    }

    .about-top-text-2{
        text-align: center;
        font-size: 34px;
    }
    
    .about-down-text{
        padding-top: 5px;
        font-size: 15px;
        text-align: justify;
    }
}


.about-inner-wrapper2{
    position:relative;
    display: flex;
    width: 100%;
    height:100%;
    overflow:hidden;
    margin-left: 40px;
    margin-right: 40px;
    /* flex-direction: column; */
}

.about-team-1{
    width: 50%;
    display: flex;
    flex-direction: column;
}

.about-team-2{
    width: 50%;
    display: inline-flex;
    /* flex-direction: column; */
}

.about-team-1-img-sec{
    position: relative;
    padding:20px 0;
    margin: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.about-team-1-img{
    width: 30%;
    border: 1px solid black;
    border-radius: 50%;
    object-fit: contain;
}

.about-team-2-img{
    border-radius: 50%;
    object-fit: contain;
}

.about-container{
    width:100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 30px;
}

.about-info{
    width: 50%;
    display: flex;
    flex-direction: column;
}