
.tech-section{
    display: block;
    height:auto;
}

.tech-wrapper {
    position:relative;
    display: flex;
    width: 100%;
    height:100%;
    overflow:hidden;
    padding-top: 65px;
}

.tech-wrapper video {
    display: flex;
    position: absolute;
    top: 65;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.tech-wrapper .vid-text{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    margin:50px;
    background-color: #fff;
    text-align: center;
    mix-blend-mode: screen;
    text-transform: uppercase;
    font-size: 100px;
    font-weight: 800;
}

@media screen and (max-width:960px){
    .tech-wrapper .vid-text{
        display: flex;
        justify-content: center;
        width: 100%;
        height: 100%;
        margin:50px;
        background-color: #fff;
        mix-blend-mode: screen;
        text-transform: uppercase;
        font-size: 50px;
        font-weight: 800;
    }
}

.tech-wrapper-2 {
    position:relative;
    display: flex;
    width: 100%;
    height:100%;
    overflow:hidden;
}

.tech-inner-wrapper{
    position:relative;
    display: flex;
    background-color: #00cdcd;
    width: 100%;
    height:100%;
    overflow:hidden;
    margin-left: 40px;
    margin-right: 40px;
}

.tech-inner-wrapper2{
    position:relative;
    display: flex;
    /* background-color: #00cdcd; */
    width: 100%;
    height:100%;
    overflow:hidden;
    margin-left: 40px;
    margin-right: 40px;
    margin-bottom: 20px;
}

.tech-inner-text {
    display: block;
    padding: 30px;
    width: 100%;
    height: 100%;
    margin: 15px;
}

