.tech-inner-text2{
    display: flex;
	flex-wrap: wrap;
	justify-content: center;
    margin-left: auto;
    margin-right: auto;
}

.tech-top-text{
    font-size: 70px;
    font-family: 'Poppins',cursive;
}

.tech-down-text{
    padding-top: 15px;
    font-size: 25px;
    text-align: justify;
    font-family: 'Poppins',cursive;
}

@media screen and (max-width:960px){
    .tech-top-text{
        font-size: 44px;
    }
    
    .tech-down-text{
        padding-top: 10px;
        font-size: 20px;
        text-align: justify;
    }
}
@media screen and (max-width:760px){
    .tech-top-text{
        font-size: 34px;
    }
    
    .tech-down-text{
        padding-top: 5px;
        font-size: 15px;
        text-align: justify;
    }
}

