.CustContainer {
    /* background-color: #fff8f2; */
    background-color:#fff;
    width:100%;
    height:100%;
    padding-top: 30px;
}
@media screen and (max-width:760px) {
    .CustContainer{
        padding:30px 0;
        width:100%;
        height:100%;
    }
}

.CustWrapper {
    display:grid;
    z-index:1;
    height:100%;
    width:100%;
    max-width:1100px;
    margin-right:auto;
    margin-left:auto;
    padding:0 24px;
    justify-content:center; 
}

.CustRow {
    display:flex;
    flex-wrap:wrap;
    /* margin-right:-15px;
    margin-left:-15px; */
    text-align: center!important;
    justify-content: center!important;
}

.CustCol {
    position: relative;
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
    margin-bottom: 15px;
    padding: 0 15px;
}

.CustText {
    padding: 0;
    text-align: center;
    position: relative;
    padding-top:20px;
    padding-bottom: 10px;
}

.CustText > span {
    /* color: #e60072; */
    color: #360c65;
    text-transform: uppercase;
    letter-spacing: .5px;
    font-size: 16px;
    font-weight: 400;
    margin: 0 0 15px;
    display: block;
}

.bigtext-cust {
    font-size: 34px;
    line-height: 46px;
    margin-top:10px;
    font-family: Poppins,sans-serif;
    color: #000;
}

.CustRow2 {
    display: flex;
	align-items: center;
	justify-content: center;
	flex-wrap: wrap;
	margin-top: 0px;
}

.CustBox {
     /* position: relative; */
  display: flex;
  /* flex-wrap: wrap; */
  justify-content: center;
  width: calc(150px - 30px);
  height: calc(110px - 30px);
  background: #fff8f2;
  float: left;
  margin: 25px;
  box-sizing: border-box;
  overflow: hidden;
  border-radius: 10px;
  margin-bottom:80px;
}