  .ps-timeline-sec .timeline-container ol.ps-timeline {
    margin: 390px 0;
    padding: 0;
    border-top: 2px solid #00cdcd;
    list-style: none;
  }
  .ps-timeline-sec .timeline-container ol.ps-timeline li {
    float: left;
    width: 25%;
    padding-top: 30px;
    position: relative;
  }
  .ps-timeline-sec .timeline-container ol.ps-timeline li span {
    width: 50px;
    height: 50px;
    margin-left: -25px;
    background: #fff;
    border: 4px solid #00cdcd;
    border-radius: 50%;
    box-shadow: 0 0 0 0px #fff;
    text-align: center;
    line-height: 50px -10;
    color: #df8625;
    font-size: 2em;
    font-style: normal;
    position: absolute;
    top: -26px;
    left: 50%;
  }
  .ps-timeline-sec .timeline-container ol.ps-timeline li span.ps-sp-top:before {
    content: "";
    color: #348e80;
    width: 2px;
    height: 50px;
    background: #00cdcd;
    position: absolute;
    top: -50px;
    left: 50%;
  }
  .ps-timeline-sec .timeline-container ol.ps-timeline li span.ps-sp-top:after {
    content: "";
    color: #348e80;
    width: 8px;
    height: 8px;
    background: #00cdcd;
    position: absolute;
    bottom: 90px;
    left: 44%;
    border-radius: 100%;
  }
  .ps-timeline-sec .timeline-container ol.ps-timeline li span.ps-sp-bot:before {
    content: "";
    color: #348e80;
    width: 2px;
    height: 50px;
    background: #00cdcd;
    position: absolute;
    bottom: -50px;
    left: 50%;
  }
  .ps-timeline-sec .timeline-container ol.ps-timeline li span.ps-sp-bot:after {
    content: "";
    color: #348e80;
    width: 8px;
    height: 8px;
    background: #00cdcd;
    position: absolute;
    top: 90px;
    left: 44%;
    border-radius: 100%;
  }
  .ps-timeline-sec .timeline-container ol.ps-timeline li .img-handler-top {
    position: absolute;
    bottom: 0;
    margin-bottom: 130px;
    width: 100%;
  }
  .ps-timeline-sec .timeline-container ol.ps-timeline li .img-handler-top img {
    display: table;
    margin: 0 auto;
  }
  .ps-timeline-sec .timeline-container ol.ps-timeline li .img-handler-bot {
    position: absolute;
    margin-top: 60px;
    width: 100%;
  }
  .ps-timeline-sec .timeline-container ol.ps-timeline li .img-handler-bot img {
    display: table;
    margin: 0 auto;
  }
  .ps-timeline-sec .timeline-container ol.ps-timeline li p {
    text-align: center;
    width: 80%;
    margin: 0 auto;
  }
  .ps-timeline-sec .timeline-container ol.ps-timeline li .ps-top {
    position: absolute;
    bottom: 0;
    margin-bottom: 100px;
    /* margin-left: 75px; */
    font-size: 20px;
    
  }
  .ps-timeline-sec .timeline-container ol.ps-timeline li .ps-bot {
    position: absolute;
    margin-top: 35px;
    margin-left: 35px;
    font-size: 20px;
  }

