@import url('https://fonts.googleapis.com/css2?family=Reggae+One&display=swap');
.section-count{
    background-color: #129fb8;
    display: block;
}

.section-count-header{
    text-align: center;
    font-size: 1.25rem;
    color: #2B2D38;
    /* background-color:lightcoral; */
    
}

.section-count-container{
    padding: 45px;
    
}

.CountText {
    padding: 0;
    text-align: left;
    position: relative;
    padding-top:20px;
    /* padding-bottom: 60px; */
}

.CountTopText{
    /* color: #e60072; */
    color: white;
    text-transform: capitalize;
    letter-spacing: .5px;
    font-size: 70px;
    font-weight: 700;
    margin: 0 0 15px;
    margin-bottom: 20px;
    display: block;
    font-family: 'Poppins',cursive;
}

@media screen and (max-width:550px){
    .CountTopText{
        font-size: 50px;
    }
}

.CountText h3 {
    font-size: 25px;
    line-height: 46px;
    font-weight: 400;
    margin-top:10px;
    font-family: 'Poppins',sans-serif;
    /* color: #050748; */
    color: white;
}

.CountSmalltext {
    color:white;
    text-transform: uppercase;
    letter-spacing:  .5px;
    font-size: 50px;
    font-weight: 400;
    margin-top: 15px;
    display: block;
}

.section-count-title{
    font-size: 2rem;
    font-weight: 600;
    font-family: "Verdana";
}



.count-boxes{
  display: flex;
  flex-direction: column;
  /* flex-wrap: wrap; */
  /* justify-content: center; */
  width: calc(250px - 30px);
  height: 100%;
  /* height: calc(550px - 10px); */
  /* float: left; */
  margin: 25px;
  box-sizing: border-box;
  overflow: hidden;
  /* border:1px solid black; */
}
.count-inner-box{
    display:flex;
    flex-direction: column;
    justify-content: center;
    width: calc(250px - 20px);
    /* height: 100%; */
    height: calc(300px - 10px);
    /* margin: 15px; */
    box-sizing: border-box;
    background-color:#03798f;
    /* border:1px solid blue; */
}
.count-down-box{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;

}
.count-imgs{
    width: 100%;
    height: 100%;
    object-fit: fill;
}
.count-column1{
    display: flex;
    flex-direction: column;
    justify-content: center;
    width:calc(300px - 30px);
    /* height: calc(600px - 30px); */
    margin: 15px;
    box-sizing: border-box;
    overflow: hidden;
    border: 0px dotted black;
}

.count-overlay {
    position: absolute;
    /* background-color:rgba(1, 191, 113, 0.8); */
    background-color:#a6ebf7;
    overflow: hidden;
    /* float: left; */
    box-sizing: border-box;
    border-radius: 0px;
    width:calc(300px - 30px);
    height: calc(400px - 50px);
    -webkit-transform: scale(0);
    -ms-transform: scale(0);
    transform: scale(0);
    -webkit-transition: .5s ease;
    transition: .5s ease;
    opacity: 0;
  }
  
  .count-column1:hover .count-overlay {
    -webkit-transform: scale(1);
    -ms-transform: scale(1);
    transform: scale(1);
    opacity: 1;
    
  }


.counters {
	display: flex;
	align-items: center;
	justify-content: center;
	flex-wrap: wrap;
	margin-top: 0px;
    /* background-color: lightgreen; */
    padding: 20px;
}

.counter{
    /* margin-top: 5px; */
    font-size: 24px;
    text-align: center;
    display: inline-block;
}

.counter-text {
    font-size: 18px;
    font-weight: 400;
    text-align: center;
}

.count-top-text{
    margin-top: 25px;
    font-size: 24px;
}

.count-middle-text{
    margin-top: 20px;
}

.button-service{
    margin-top:20px;
    width:50%;
    height:35px;
    border-radius: 24px;
    direction: none;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 30px;
    align-self: center;
    outline: black;
    border:2px solid black;
    cursor:pointer;
    display:flex;
    justify-content: center;
    align-items:center;
    padding: 14px 48px;
    white-space:nowrap;
    font-size: 15px;
    transition: all 0.2s ease-in-out;
}
.button-service:hover {
    transition: all 0.2s ease-in-out;
    background-color: blueviolet;
    color: #fff;
    border:2px solid blueviolet;
}

