.sidebarWrapper
{
    color:#fff;
    width: 100%;
}


.sideMenu
{
    display:grid;
    width: 100%;
    grid-template-columns:1fr;
    grid-template-rows:repeat(6, 80px);
    text-align:center;
}
@media screen and (max-width:480px) {
    .sideMenu{
        grid-template-rows: repeat(6,60px);
    }
}