footer{
	position: fixed;
	bottom: 0;
}

@media (max-height:800px){
	footer { position: static; }
	header { padding-top:40px; }
}


.footer-distributed{
	background-color: #2c292f;
	box-sizing: border-box;
	width: 100%;
	text-align: left;
	font: bold 16px sans-serif;
	padding: 50px 50px 60px 50px;
	/* margin-top: 80px; */
}

.footer-distributed .footer-left,
.footer-distributed .footer-center,
.footer-distributed .footer-right,
.down-footer .footer-center-2{
	display: inline-block;
	vertical-align: top;
}

/* Footer left */

/* .footer-distributed .footer-left{
	width: 30%;
} */

.text-footer{
	color:  #ffffff;
	font: normal 36px 'Cookie', cursive;
	margin: 0;
	text-align: center;
}

/* The company logo */

.footer-left{
	width: 30%;
	margin-left: 0;
}


.text-footer span{
	color:  #129fb8;
}

/* Footer links */

 .footer-links{
	color:  #ffffff;
	margin: 20px 0 12px;
	text-align: center;
}

 .footer-links a{
	display:inline-block;
	line-height: 1.8;
	text-decoration: none;
	color:  inherit;
}

.footer-links .footer-hover:hover {
	color: #00cdcd;
}

.footer-company-name{
	color:  #8f9296;
	font-size: 14px;
	font-weight: normal;
	margin: 0px;
	text-align: center;
}

/* Footer Center */

.footer-distributed .footer-center{
	width: 35%;
	/* border: 2px solid lightgreen; */
	/* padding: 10px; */
	
}

.footer-center-2 {
	width: 35%;
	margin-left:auto;
	margin-right: auto;
}


.footer-distributed .footer-center i{
	background-color:  #33383b;
	color: #ffffff;
	font-size: 25px;
	width: 38px;
	height: 38px;
	border-radius: 50%;
	text-align: center;
	line-height: 42px;
	margin: 10px 15px;
	vertical-align: middle;
}


.footer-distributed .footer-center i.fa-envelope{
	font-size: 17px;
	line-height: 38px;
}

.footer-distributed .footer-center p{
	display: inline-block;
	color: #ffffff;
	vertical-align: middle;
	margin:0;
}

.footer-distributed .footer-center .text-center-footer{
	display:block;
	font-weight: normal;
	font-size:16px;
	line-height:2;
	color: #ffffff;
}

.footer-distributed .footer-center p a{
	/* color:  #01bf71; */
	color: #ffffff;
	text-decoration: none;;
}

.footer-distributed .footer-center i .footer-contact-hover:hover {
	color: #00cdcc;
}


/* Footer Right */

.footer-distributed .footer-right{
	width: 35%;
	padding-left: 40px;
}

.footer-distributed .footer-company-about{
	line-height: 20px;
	color:  #92999f;
	font-size: 16px;
	font-weight: normal;
	margin: 0;
}

.footer-distributed .footer-company-about span{
	display: block;
	color:  #ffffff;
	font-size: 20px;
	font-weight: bold;
	margin-bottom: 20px;
}

.footer-distributed .footer-icons{
	margin-top: 25px;
}

.footer-distributed .footer-icons a{
    display: inline-flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
	width: 35px;
	height: 35px;
	cursor: pointer;
	background-color:  #33383b;
	border-radius: 2px;

	font-size: 20px;
	color: #ffffff;
	text-align: center;
	line-height: 35px;

	margin-right: 10px;
	margin-bottom: 5px;
}
.footer-icons > a:hover{
	background-color:#00cdcc;
}

/* Here is the code for Responsive Footer */
/* You can remove below code if you don't want Footer to be responsive */


@media (max-width: 880px) {

	.footer-distributed .footer-left,
	.footer-distributed .footer-center,
	.footer-distributed .footer-right,
	.down-footer .footer-center-2{
		display: block;
		width: 100%;
		margin-bottom: 40px;
		text-align: center;
	}

	.footer-distributed .footer-center i{
		margin-left: 0;
	}

}

/*down-footer*/
.down-footer {
	margin-left:auto;
	margin-left: auto;
	margin-top: 24px;
}

.footer-distributed .footer-center .footer-company-contact {
