.count-overlay {
    position: absolute;
    /* background-color:rgba(1, 191, 113, 0.8); */
    background-color:#a6ebf7;
    overflow: hidden;
    /* float: left; */
    box-sizing: border-box;
    border-radius: 0px;
    width:calc(300px - 30px);
    height: calc(400px - 50px);
    transform: scale(0);
    transition: .5s ease;
    opacity: 0;
  }
  
  .count-column1:hover .count-overlay {
    transform: scale(1);
    opacity: 1;
    
  }


