}

.service-info-down-text{
    padding-top: 15px;
    font-size: 25px;
    text-align: justify;
    color: #000;
    font-family: 'Poppins',cursive;
}

.service-page-image{
    width:50%;
    padding-top: 50px;
    /* background-color: #ffeefe; */
