}

.service-info-button{
    margin-top:40px;
    padding: 20px;
    background-color: #360c65;
    color: white;
    width:250px;
    border-radius: 30px;
    direction: none;
    outline: black;
    border:1px solid white;
    cursor:pointer;
    white-space:nowrap;
    font-size: 20px;
    transition: all 0.2s ease-in-out;
}

.service-info-button:hover{
    transition: all 0.2s ease-in-out;
    background-color: blueviolet;
    color: #fff;
    border:1px solid blueviolet;
}

.service-image{
    width:100%;
    height:100%;
    object-fit: contain;
}

@media screen and (max-width: 991px){
