.contact-section{
    background-color: #00cdcd;
    display: block;
    padding-top:80px ;
    height: 100%;
}

.contact-section-header{
    text-align: center;
    font-size: 1.25rem;
    color: #2B2D38;
    background-color:#00cdcd;
    height:100%;
    
}

.contact-section-container{
    padding: 30px;
}

.contact-section-title {
    margin-top: 40px;
    color: white;
    font-size: 40px;
    font-family: Arial, Helvetica, sans-serif;
}

.contact-section-text {
    color: white;
    font-size: 26px;
    padding: 40px;
    font-family: Arial, Helvetica, sans-serif;
}

.form-text{
    margin-top:20px;
    padding: 10px;
    margin-right: 40px;
    width: 350px;
    border: 2px solid black;
}

.contact-section-form{
    padding: 10px 40px 80px 40px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;

}

