.button-contact{
    margin-top:20px;
    padding: 10px;
    width:250px;
    border-radius: 24px;
    direction: none;
    outline: black;
    border:2px solid black;
    cursor:pointer;
    white-space:nowrap;
    font-size: 15px;
    transition: all 0.2s ease-in-out;
}

.button-contact:hover {
    transition: all 0.2s ease-in-out;
    background-color: blueviolet;
    color: #fff;
    border:2px solid blueviolet;
}