.Hero-section{
    /* position: relative; */
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.Hero-section-wrapper{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
   
}

.hero-img{
    width: 100%;
    height: 100vh;
    object-fit: cover;
    /* opacity: 0.5; */
}

.hero-text-bottom{
    position: absolute;
    width:100%;
    display: flex;
    flex-direction: column;
    top: 10px;
    /* margin: 60px; */
    padding: 60px;
    padding-top: 0;
    
}

.TopLine {
    color:#360c65;
    font-size:40px;
    line-height:40px;
    font-weight:700;
    letter-spacing:1.4px;
    text-transform:uppercase;
    text-align: center;
    margin-bottom:20px;
}


.hero-card{
    position: relative;
    width:80%;
    height:350px;
    background: #333;
    border-radius: 14px;
    color:#fff;
    font-size: 32px;
    /* margin:0 20px; */
    margin-top: 80px;
    justify-content:center;
    align-items: center;
}

.hero-text-bottom h3 {
    font-size: 150px;
    font-weight: bold;
    color: #fff;
}

.hero-text-bottom span{
    color: #129fb8;
}