.service-page-section{
    display: block;
    height:auto;
}

.service-page-wrapper {
    position:relative;
    display: flex;
    width: 100%;
    height:100%;
    overflow:hidden;
    padding-top: 65px;
}

.service-page-wrapper video {
    display: flex;
    position: absolute;
    top: 65;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.service-page-wrapper .service-page-text{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    margin:50px;
    background-color: #fff;
    text-align: center;
    mix-blend-mode: screen;
    text-transform: uppercase;
    font-size: 100px;
    font-weight: 800;
}

@media screen and (max-width:960px){
    .service-page-wrapper .service-page-text{
        display: flex;
        justify-content: center;
        width: 100%;
        height: 100%;
        margin:50px;
        background-color: #fff;
        mix-blend-mode: screen;
        text-transform: uppercase;
        font-size: 50px;
        font-weight: 800;
    }
}

.service-page-wrapper-2 {
    position:relative;
    display: flex;
    width: 100%;
    height:100%;
    overflow:hidden;
}

.service-page-inner-wrapper{
    position:relative;
    display: flex;
    background-color: #360c65;
    width: 100%;
    height:100%;
    overflow:hidden;
    margin-left: 40px;
    margin-right: 40px;
}


.service-page-inner-text {
    display: block;
    padding: 30px;
    width: 100%;
    height: 100%;
    margin: 15px;
    text-align: center;
}


.service-page-top-text{
    font-size: 70px;
    color: #fff;
    text-align: left;
    font-family: 'Poppins',cursive;
}

.service-page-down-text{
    padding-top: 15px;
    font-size: 25px;
    text-align: justify;
    margin-bottom: 40px;
    color: #fff;
    font-family: 'Poppins',cursive;
}



.service-page-inner-wrapper-2{
    position:relative;
    display: flex;
    /* background-color: #00cdcd; */
    width: 100%;
    height:100%;
    overflow:hidden;
    margin-left: 40px;
    margin-right: 40px;
}

.service-page-container{
    width:100%;
    display: flex;
    justify-content: center;
    align-items: center;
    /* margin-top: 30px; */
}

.service-page-container .service-page-info {
    width: 50%;
    display: flex;
    flex-direction: column;
    /* background-color: aqua; */
    margin-bottom: 10px;
}

.service-page-container .service-page-info .service-info-text{
    /* width: 100%;
    padding:20px;
    display: flex;
    flex-direction: column;
    border-radius: 10px;
    border: 2px dotted black; */
   

    
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    padding-top: 50px;
    height: calc(450px - 30px);
    background: #fff;
    float: left;
    box-sizing: border-box;
    overflow: hidden;
    border-radius: 15px;
    margin-bottom:60px;

}

.service-page-container .service-page-image .service-info-text{
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    height: calc(450px - 30px);
    /* padding: 20px; */
    background: #fff;
    float: left;
    box-sizing: border-box;
    overflow: hidden;
    border-radius: 15px;
    margin-bottom:60px;
    /* margin-left: 80px; */
}

.service-page-container .service-info-text{
    padding: 0;
}

.service-info-top-text{
    font-size: 44px;
    color: #000;
    font-family: 'Poppins',cursive;
}

.service-info-down-text{
    padding-top: 15px;
    font-size: 25px;
    text-align: justify;
    color: #000;
    font-family: 'Poppins',cursive;
}

.service-page-image{
    width:50%;
    padding-top: 50px;
    /* background-color: #ffeefe; */
    margin-bottom: 0px;
}

.service-info-button{
    margin-top:40px;
    padding: 20px;
    background-color: #360c65;
    color: white;
    width:250px;
    border-radius: 30px;
    direction: none;
    outline: black;
    border:1px solid white;
    cursor:pointer;
    white-space:nowrap;
    font-size: 20px;
    transition: all 0.2s ease-in-out;
}

.service-info-button:hover{
    transition: all 0.2s ease-in-out;
    background-color: blueviolet;
    color: #fff;
    border:1px solid blueviolet;
}

.service-image{
    width:100%;
    height:100%;
    object-fit: contain;
}

@media screen and (max-width: 991px){
    .service-page-container{
        flex-direction: column;
    }

    .service-page-container .service-page-info{
        margin-bottom: 10px;
    }

    .service-page-container .service-page-image{
        margin-bottom: 60px;
        display: none;
    }

    .service-page-container .service-page-info,
    .service-page-container .service-page-image{
        width: 100%;
        /* height: calc(350px - 30px); */
    }
}

@media screen and (max-width:960px){
    .service-page-top-text{
        font-size: 44px;
    }


    .service-page-down-text{
        padding-top: 10px;
        font-size: 20px;
        text-align: justify;
    }

    .service-info-top-text{
        font-size: 34px;
    }

    .service-info-down-text{
        padding-top: 10px;
        font-size: 20px;
        text-align: left;
    }
}
@media screen and (max-width:760px){
    .service-page-top-text{
        font-size: 34px;
    }

    
    .service-page-down-text{
        padding-top: 5px;
        font-size: 15px;
        text-align: justify;
    }

    .service-info-top-text{
        font-size: 24px;
    }

    .service-info-down-text{
        padding-top: 5px;
        font-size: 15px;
        text-align: left;
    }
}
