.ProjContainer {
    color:#000;
    /* background-color: #fbf9ed; */
    background-color: #fff;
    z-index:1;
    width:100%;
    height:100%;
}
@media screen and (max-width:760px) {
    .ProjContainer{
        /* padding:40px 0; */
    width:100%;height:100%;}
}

.ProjWrapper {
    display:grid;
    z-index:1;
    height:100%;
    width:100%;
    max-width:100%;
    margin-right:auto;
    margin-left:auto;
    padding:45px;
    /* justify-content:center; */
    
    
}

.ProjRow {
    display:grid;
    grid-auto-columns:minmax(auto,1fr);
    align-items:center;
    grid-template-areas: 'col1 col1 col2 col2';
    
}
@media only screen and (max-width:768px) {
	.ProjRow{
		grid-template-areas:'col1 col1'
							'col2 col2';
	}
}

.Col-1 {
    margin-bottom: 15px;
    padding: 0 15px;
    grid-area:col1;
}

.Col-2 {
    /* margin-left:10px; */
    margin-bottom: 15px;
    padding: 0 15px;
    grid-area:col2;
    
    
}
.colContainer {
    margin-top: 20px;
    display:grid;
    gap:20px;
    grid-template-columns:repeat(auto-fill,minmax(150px,1fr));
    grid-template-rows:1fr;
}

.ProjTextWrapper {
    max-width: 1040px;
    padding-top:30px;
    /* padding-bottom: 60px; */
}

@media screen and (max-width:520px) {
    .ProjTextWrapper{padding-top: 0;}
    
}

.ProjTopLine {
    color:#360c65;
    font-size:70px;
    line-height:80px;
    font-weight:700;
    /* letter-spacing:1.4px; */
    font-family: 'Poppins',cursive;
    text-transform:capitalize;
    /* margin-bottom:20px; */
}

@media screen and (max-width:520px) {
    .ProjTopLine{font-size:50px;}
    
}

.ProjHeadLine {
    margin-top: 30px;
    margin-bottom:24px;
    font-size:35px;
    line-height:1.1;
    /* font-weight:600; */
    font-family: 'Poppins',cursive;
    color:black;
}
@media screen and (max-width:480px) {
    .ProjHeadLine{font-size:32px;}
    
}

.ProjSubtitle {
    max-width:440px;
    margin-bottom:35px;
    font-size:18px;
    line-height:24px;
    color:black;
}

.zone {
    cursor:pointer;
