.Count-Row {
    display:grid;
    grid-auto-columns:minmax(auto,1fr);
    align-items:center;
    grid-template-areas: 'col-1 col-1 col-1 col-2 col-2 col-2';
    margin-left: auto;
    margin-right: auto;
    padding: 40px;
    padding-bottom: 0;
      
}
@media only screen and (max-width:768px) {
	.Count-Row{
		grid-template-areas:'col-1 col-1 col-1'
							'col-2 col-2 col-2';
	}
}

.Count-Col1 {
    /* margin: 35px; */
    /* padding: 0 15px; */
    padding-right: 10px;
    grid-area:col-1;
}

.Count-Col2 {
    /* margin-left:10px; */
    /* margin: 35px; */
    /* padding: 0 15px; */
