    font-size: 38px;
    line-height: 57px;
    font-family: Poppins,sans-serif;
    color: #050748;
    font-weight: 400;
    /* padding: 40px; */
}

.CountButton {
    align-self: center;
    margin-left: auto;
    margin-right:auto ;
    border-radius:50px;
    outline:blueviolet;
    border:2px solid blueviolet;
    cursor:pointer;
    display:flex;
    justify-content: center;
    align-items:center;
    padding: 10px 28px;
    margin-top: 15px;
    white-space:nowrap;
    font-size: 20px;
    transition: all 0.2s ease-in-out;
    background-color: blueviolet;
    color: #fff;
}
