.ContactUsContainer {
    /* background-color: #fbf9ed; */
    background-color: #360c65;
    width:100%;
    height:100%;
    padding-top: 60px;
}
@media screen and (max-width:760px) {
    .ContactUsContainer{
        padding:100px 0;
        width:100%;
        height:100%;
    }
}

.ContactUsWrapper {
    display:grid;
    z-index:1;
    height:100%;
    width:100%;
    max-width:1100px;
    margin-right:auto;
    margin-left:auto;
    padding:0 24px;
    justify-content:center; 
}

.ContactUsRow {
    display:flex;
    flex-wrap:wrap;
    /* margin-right:-15px;
    margin-left:-15px; */
    text-align: center!important;
    justify-content: center!important;
}

.ContactUsCol {
    position: relative;
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
    margin-bottom: 15px;
    padding: 0 15px;
}

.ContactUsText {
    padding: 0;
    text-align: center;
    position: relative;
    padding-top:20px;
    padding-bottom: 60px;
    font-family: 'Poppins',cursive;
}

.ContactUsText > span {
    /* color: #e60072; */
    color: white;
    text-transform: uppercase;
    letter-spacing: .5px;
    font-size: 16px;
    font-weight: 400;
    margin: 0 0 15px;
    display: block;
    font-family: 'Poppins',cursive;
}

.ContactUsbigtext {
    font-size: 34px;
    line-height: 46px;
    margin-top:10px;
    font-family: 'Poppins',cursive;
    /* color: #050748; */
    color: white;
}

.WorkRow2 {
    display: flex;
	align-items: center;
	justify-content: center;
	flex-wrap: wrap;
    margin-top: 0px;
    width:100%;
}

.ContactUsbutton{
    margin-top:40px;
    padding: 20px;
    background-color: #360c65;
    color: white;
    width:250px;
    border-radius: 30px;
    direction: none;
    outline: black;
    border:1px solid white;
    cursor:pointer;
    white-space:nowrap;
    font-size: 20px;
    transition: all 0.2s ease-in-out;
    font-family: 'Poppins',cursive;
}

.ContactUsbutton:hover {
    transition: all 0.2s ease-in-out;
    background-color: blueviolet;
    color: #fff;
    border:1px solid blueviolet;
}