.product-section{
    display: block;
    height:auto;
}

.product-wrapper {
    position:relative;
    display: flex;
    width: 100%;
    height:100%;
    overflow:hidden;
    padding-top: 65px;
}

.product-wrapper video {
    display: flex;
    position: absolute;
    top: 65;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.product-wrapper .product-text{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    margin:50px;
    background-color: #fff;
    text-align: center;
    mix-blend-mode: screen;
    text-transform: uppercase;
    font-size: 100px;
    font-weight: 800;
}

@media screen and (max-width:960px){
    .product-wrapper .product-text{
        display: flex;
        justify-content: center;
        width: 100%;
        height: 100%;
        margin:50px;
        background-color: #fff;
        mix-blend-mode: screen;
        text-transform: uppercase;
        font-size: 50px;
        font-weight: 800;
    }
}