*{
    margin:0;
    padding: 0;
    box-sizing: border-box;
}

.navigation{
    padding: 20px 40px;
    /* background: #9e00ff; */
    /* background: #abb0b1; */
    width: 100%;
    height: 65px;
    display:flex;
    justify-content:space-between;
    align-items:center;
    font-size:1rem;
    position:fixed;
    top:0;
    left: 0;
    z-index:10;
    box-sizing: border-box;
    transition: .5s;
}

.navigation.black {
    /* background: rgba(0,0,0,.8); */
    background-color: #e9e4eb;
}

.navigation.black .navigation-list li a{
    color: #000;
}
.nav-image {
    width:200px;
    height:68px;
    /* bottom: 0; */
    object-fit: contain;
    /* transition: .5s; */
    /* visibility: hidden; */
}

.navigation.black .nav-image {
    visibility: visible;
}
.navigation-mobileIcon
{
    display:none;
}

.navigation.black .navigation-mobileIcon{
    color: #000;
}
@media screen and (max-width:850px) {
    .navigation-mobileIcon{
        display:block;
        position:absolute;
        top:0;
        right:0;
        transform:translate(-100%, 50%);
        font-size:1.6rem;
        cursor:pointer;
        color:#000;
    }
    .nav-image {
        width:150px;
        height:68px;
        bottom: 0;
        object-fit: contain;
    }
}

.navigation-list{
    display: flex;
    list-style: none;
}

.navigation-list li{
    margin-left: 30px;
}

.navigation-list li a{
    text-decoration:none;
    text-transform: uppercase;
    font-family: "Verdana";
    font-size: 16px;
    color:#000;
}


@media screen and (max-width:850px){

    .navigation{
        padding-left:0;
    }

    .navigation-list{
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: #9e00ff;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        transform: translateX(-100%);
        /* transition: transform 0.3s; */
    }

    .navigation-list li{
        margin-left: 10px;
    }

    .navigation-list li a{
        display: inline-block;
        font-size: 10px;
        padding: 15px 10px;
        transition: all 0.3s;
    }

    .navigation-list li a:hover{
        background-color: #fff;
        color: #9e00ff;
        transform: translateY(-10px);
    }
    
}

