.section{
    background-color: #fff;
    display: block;
    padding-top: 80px;
}

.section-header{
    text-align: center;
    font-size: 1.25rem;
    color: #2B2D38;
    background-color:#fff;
    
}

.section-container{
    padding: 30px;
}

.section-title{
    font-size: 2rem;
    font-weight: 600;
    font-family: "Verdana";
}

.service-bottom {
	display: flex;
	align-items: center;
	justify-content: center;
	flex-wrap: wrap;
	margin-top: 0px;
    background-color: #fff;
    padding: 30px;
}

.column1{
    display: flex;
    flex-direction: column;
    justify-content: center;
    width:calc(500px - 30px);
    /* height: calc(600px - 30px); */
    margin: 15px;
    box-sizing: border-box;
    overflow: hidden;
    /* border: 1px solid black; */
}

.boxes{
    display: flex;
    flex-direction: column;
    /* flex-wrap: wrap; */
    /* justify-content: center; */
    width: calc(450px - 30px);
    height: 100%;
    /* height: calc(550px - 10px); */
    float: left;
    margin: 15px;
    box-sizing: border-box;
    overflow: hidden;
    /* border:1px solid black; */
  }


.inner-box{
      display:flex;
      flex-direction: column;
      justify-content: center;
      width: calc(450px - 30px);
      height: calc(400px - 10px);
      /* margin: 15px; */
      box-sizing: border-box;
      /* border:1px solid blue; */
}

.top-text{
    margin-top: 25px;
    font-size: 24px;
}

.middle-text{
    margin-top: 20px;
}

.button-service{
    margin-top:20px;
    width:50%;
    height:35px;
    border-radius: 24px;
    direction: none;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 30px;
    align-self: center;
    outline: black;
    border:2px solid black;
    cursor:pointer;
    display:flex;
    justify-content: center;
    align-items:center;
    padding: 14px 48px;
    white-space:nowrap;
    font-size: 15px;
    transition: all 0.2s ease-in-out;
}
.button-service:hover {
    transition: all 0.2s ease-in-out;
    background-color: blueviolet;
    color: #fff;
    border:2px solid blueviolet;
}

.imgs{
    width: 100%;
    height: 100%;
    object-fit: fill;
}

