*{
    margin:0;
    padding: 0;
    box-sizing: border-box;

}

nav{
    padding: 20px 40px;
    /* background: #9e00ff; */
    /* background: #abb0b1; */
    width: 100%;
    height: 65px;
    display:flex;
    justify-content:space-between;
    align-items:center;
    font-size:1rem;
    position:fixed;
    top:0;
    left: 0;
    z-index:10;
    box-sizing: border-box;
    transition: .5s;
}

nav.black {
    /* background: rgb(145, 135, 135); */
    background-color: #e9e4eb;
    width: 100%;
}

nav.black .nav-list li a{
    color: #000;
}
.imgn {
    width:100px;
    height:68px;
    /* bottom: 0; */
    object-fit: contain;
    /* transition: .5s; */
    visibility: hidden;
    /* display: flex;
    justify-content: center; */
	/* font: normal 20px 'Cookie', cursive;
	text-align: center; */
}

.logoname{
    margin-right: 500px;
}

nav.black .imgn {
    visibility: visible;
}
.mobileIcon
{
    display:none;
}
nav.black .mobileIcon{
    color: #000;
}
@media screen and (max-width:850px) {
    .mobileIcon{
        display:block;
        position:absolute;
        top:0;
        right:0;
        transform:translate(-100%, 50%);
        font-size:1.6rem;
        cursor:pointer;
        color:#fff;
    }
    .imgn {
        width:150px;
        height:68px;
        bottom: 0;
        object-fit: contain;
    }
}

.nav-list{
    display: flex;
    list-style: none;
}

.nav-list li{
    margin-left: 30px;
}

.nav-list li a{
    text-decoration:none;
    text-transform: uppercase;
    font-family: "Verdana";
    font-size: 16px;
    color:#fff;
}


@media screen and (max-width:850px){

    .nav{
        padding-left:0;
    }

    .nav-list{
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: #9e00ff;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        transform: translateX(-100%);
        /* transition: transform 0.3s; */
    }

    .nav-list li{
        margin-left: 10px;
    }

    .nav-list li a{
        display: inline-block;
        font-size: 10px;
        padding: 15px 10px;
        transition: all 0.3s;
    }

    .nav-list li a:hover{
        background-color: #fff;
        color: #9e00ff;
        transform: translateY(-10px);
    }
    
}

section.sec1{
    width: 100%;
    height: 100vh;
    background: url('../../images/bg7.jpg');
    background-size: cover;
}

.sec1-text{
    position: absolute;
    width:100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    top: 10px;
    padding: 60px;
}

.sec1-heading{
	color:  #ffffff;
    display: flex;
    justify-content: center;
	font: normal 200px 'Cookie', cursive;
    padding-top: 20px;
	margin: 0;
	text-align: center;
}

.sec1-heading span{
	color:  #129fb8;
}



.sec1-card{
    position: relative;
    width:80%;
    height:350px;
    background: #333;
    border-radius: 14px;
    color:#fff;
    /* font-size: 32px; */
    /* margin:0 20px; */
    margin-top: 100px;
    justify-content:center;
    align-items: center;
}
.sec1-card img{
    /* position: absolute; */
    width:100%;
    height: 100%;
    object-fit: cover;
    border-radius: 14px;
}

.sec1-carousel{
    position: relative;
    margin-left: 490px;
}

.sec1-card-overlay {
    position: absolute;
    background-color:rgba(54, 12, 101, 0.8);
    overflow: hidden;
    float: left;
    box-sizing: border-box;
    border-radius: 10px;
    width:100%;
    height:350px;
    -webkit-transform: scale(0);
    -ms-transform: scale(0);
    transform: scale(0);
    -webkit-transition: .5s ease;
    transition: .5s ease;
    opacity: 0;
  }
  
  .sec1-card:hover .sec1-card-overlay {
    -webkit-transform: scale(1);
    -ms-transform: scale(1);
    transform: scale(1);
    opacity: 1;
    
  }
  .sec1-card-text {
    color: white;
    font-size: 25px;
    font-weight: 700;
    position: absolute;
    top: 40%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    text-align: center;
    text-transform: uppercase;
  }


  @media screen and (max-width:1500px){
